.news {
	overflow-x: hidden;

	.wrapper {
		display: flex;
		flex-direction: column;
		align-items: flex-start; }

	&__text {
		position: relative;
		max-width: 1100px;
		margin: 0 auto;
		padding: 65px 0;
		margin-bottom: 100px;

		@include desktop {
			max-width: 960px; }

		&:before {
			content: '';
			position: absolute;
			left: 50%;
			top: 0;
			width: 100vw;
			height: 100%;
			background-color: #fcfcfc;
			transform: translateX(-50%);
			z-index: -1; } }

	&__btn {
		width: 326px;
		align-self: center;
		margin-bottom: 6px;
		margin-top: 65px;

		@include pad {
			width: 250px; } } }
