.index {
	.header__bottom {
		padding-bottom: 0; }

	.icatalog {
		padding-top: calc(100vh - 455px);
		transition: .5s padding;
		user-select: none;

		@include desktop {
			padding-top: calc(100vh - 441px); }

		@include sdesktop {
			padding-top: 100px; }

		.icatalog__footer {
			opacity: 0;
			transition: all .5s;

			@include sdesktop {
				opacity: 1; } }

		.icatalog__slider {
			.swiper-buttons {
				opacity: 0;
				transition: all .5s;

				@include sdesktop {
					opacity: 1; } } }

		&.show {
			padding-top: 200px;
			user-select: none;

			@include sdesktop {
				padding-top: 100px; }

			.icatalog__footer {
				opacity: 1;
				transition-delay: .2s; }

			.icatalog__slider {
				.swiper-buttons {
					opacity: 1;
					transition-delay: .2s; } }

			.icatalog__imgs-item {
				&:before {
					width: 0;
					transition-delay: .2s; } } } } }

.first-screen {
	position: fixed;
	width: 0;
	left: 0;
	top: 0;
	width: 100%;
	height: 100vh;
	transition: .5s transform;
	transform: translateY(0);
	z-index: 10;

	@include sdesktop {
		position: static; }

	&.hide {
		transform: translateY(-100%);
		@include sdesktop {
			transform: translateY(0); }

		.hero__catalog-items {
			opacity: 0; } }

	&.show {
		transform: translateY(0);

		.hero__catalog-items {
			opacity: 1; } } }
