.cards {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-right: -36px;
	width: calc(100% + 36px);


	@include desktop {
		margin-right: -32px;
		width: calc(100% + 32px); }

	@include mob {
		width: 100%;
		margin-right: 0; }

	&__item {
		width: calc(100% / 3 - 36px);
		margin-right: 36px;
		display: flex;
		flex-direction: column;
		margin-bottom: 98px;

		&:nth-last-child(1),
		&:nth-last-child(2),
		&:nth-last-child(3) {
			margin-bottom: 0; }

		@include desktop {
			width: calc(100% / 3 - 32px);
			margin-right: 32px; }

		@include pad {
			width: calc(100% / 2 - 32px); }

		@include mob {
			width: 100%;
			margin-right: 0;
			margin-bottom: 40px;

			&:nth-last-child(1),
			&:nth-last-child(2),
			&:nth-last-child(3) {
				margin-bottom: 40px; }

			&:last-child {
				margin-bottom: 0; } } }

	&__over-img {
		position: relative;
		font-size: 0;
		line-height: 0;
		margin-bottom: 21px;
		transition: .8s opacity;

		&:hover {
			opacity: .8; } }

	&__over-link {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0; }

	&__over-pic {
		object-fit: cover;
		font-family: 'object-fit: cover;';
		width: 100%;
		height: 266px;

		@include desktop {
			height: 240px; }

		@include pad {
			height: 280px; }

		@include mob {
			height: 240px; } }

	&__data {
		font-size: 14px;
		line-height: 18px;
		color: $black;
		margin-bottom: 11px;

		@include desktop {
			font-size: 13px;
			line-height: 17px; } }

	&__link {
		display: inline-block;
		margin-bottom: 20px;

		span {
			font-size: 22px;
			line-height: 26px;
			font-weight: 500;
			color: $main-blue;
			border-bottom: 1px solid $border-bottom;
			transition: .5s border-color;

			@include desktop {
				font-size: 20px;
				line-height: 24px; }

			&:hover {
				border-color: $t; } } }

	&__text {
		font-size: 15px;
		line-height: 21px;
		color: $black;

		@include desktop {
			font-size: 14px;
			line-height: 20px; } } }
